import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Group, Div, Avatar, PanelHeader, Counter, List } from '@vkontakte/vkui';
import Icon24Like from '@vkontakte/icons/dist/24/like';

const Home = ({ id, go, fetchedUser, user }) => (
	<Panel id={id}>
		<PanelHeader>Конкурс на активность</PanelHeader>
		{fetchedUser &&
		<Group title="Общая информация">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={80}/> : null}
				description={"Участник конкурса"}>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group title="Количество баллов">	
				<List>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки</ListItem>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки первым</ListItem>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки в числе первых</ListItem>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Лайки всех постов</ListItem>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Хорошие посты</ListItem>
					<ListItem before={<Icon24Like />} indicator={<Counter type="primary">{user.p_flikes}</Counter>}>Хорошие комментарии</ListItem>
				</List>					
		</Group>
	</Panel>
);

export default Home;
